

:root {
  --portfolio-header-color: #1D1D1D;
}

.portfolio {
  height: 200vh;
}

.portfolio-projects {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.contact-form h2 {
  color: var(--portfolio-header-color);
  font-size: 2.5rem;
  margin-bottom: 20px;
}